import React from "react";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC, AshowDemoHOC } = HOC;
import doc from "zerod/components/ZdetailSimpleBaseHOC/doc.md";
import getDetail from "@/views/Main/HOC-doc/ZsearchListHOC-doc/demos/getDetailPage.js";

export default AmdDocHOC(doc, {
	demo1: function() {
		const Com = getDetail({
			insertLocation: "mainModal",
			headerTitle: "详情页",
			headerContent: "页面描述",
			panelAfterRender: function() {
				return (
					<div className="z-panel z-margin-top-20">
						<div className="z-panel-body">panelAfterRender</div>
					</div>
				);
			},
		});
		const MyScript = AshowDemoHOC(<Com detailId={1} />);
		return <MyScript />;
	},
});
