import React from "react";
// import { Input, InputNumber, Button } from "antd";
import { ZdetailSimpleBaseHOC } from "zerod";
import api from "@/App.api.js";
import DetailTab from "./DetailTab";

function getPage({ insertLocation, headerTitle, headerContent, panelAfterRender }) {
	const pageCofig = {
		insertLocation: insertLocation ? insertLocation : "mainModal_top",
		pageHeader: {
			show: true,
			// array>[object] | null,如果是null则不显示面包屑
			breadcrumbRoutes: null,
			// any
			title: headerTitle,
			//any
			content: headerContent,
			//element | node
			rightMoreContent: <div>右边</div>,
		},
		detail: {
			panelHeader: "基础信息",
			items: [
				{
					key: "serviceName",
					label: "服务名称",
				},
				{
					key: "serviceCode",
					label: "服务编码",
				},
				{
					key: "servicePort",
					label: "约定端口号",
					render: (value, record) => {
						return <span className="z-text-red">{value}</span>;
					},
				},
				{
					key: "remark",
					label: "服务描述",
					span: 24,
					render: (value, record) => {
						return value;
					},
				},
			],
			detailApiInterface: (id, props) => {
				return api.config.getServiceDetail({ serviceId: id });
			},
		},
		panelAfterRender:
			typeof panelAfterRender == "function"
				? panelAfterRender
				: (detail) => {
						return <DetailTab data={detail} />;
				  },

		panelBeforeRender: function() {
			return (
				<div className="z-panel z-margin-bottom-20">
					<div className="z-panel-body">panelBeforeRender</div>
				</div>
			);
		},
		moreContentRender: function() {
			return (
				<div className="z-panel z-margin-top-20">
					<div className="z-panel-body">moreContentRender</div>
				</div>
			);
		},
	};
	return ZdetailSimpleBaseHOC(pageCofig);
}
export default getPage;
